<template>
    <div class="breadcrumbs">
        <router-link :key="breadcrumb.name"
                     v-for="breadcrumb in breadcrumbs"
                     tag="a" :to="{name: breadcrumb.name}">
            / {{ breadcrumb.breadcrumb }}
        </router-link>
    </div>
</template>

<script>
    export default {
        name: "Breadcrumbs",
        props: {
            rootBreadcrumb: {
                type: Object,
                default: () => {}
            }
        },
        data() {
            return {
                breadcrumbs: [],
            }
        },
        created() {
            console.log(this.rootBreadcrumb);
            if (this.rootBreadcrumb && this.rootBreadcrumb.breadcrumb && this.rootBreadcrumb.name) {
                this.breadcrumbs.push(this.rootBreadcrumb)
            }

            this.breadcrumbs.push({
                breadcrumb: this.$route.meta.breadcrumb,
                name: this.$route.name
            });
        }
    }
</script>

